import React, { useContext, useCallback, useEffect, useRef, useState } from 'react';
import {
  View, Text, ScrollView, FlatList, StatusBar, Animated, Easing, TouchableOpacity,
} from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { Ionicons } from '@expo/vector-icons';
import { useFocusEffect } from '@react-navigation/native';
import { AIContext } from '../contexts/AIContext';
import { AuthContext } from '../contexts/AuthContext';
import HistoryCard from '../components/HistoryCard';
import GradientButton from '../components/GradientButton';
import { IconBadge, GlowIcon, CircleIcon } from '../components/AppIcon';
import { COLORS, GRADIENTS, SPACING, RADIUS, FONTS, SHADOWS } from '../constants/theme';

const QUOTES = [
  'Your child is building a brighter future, one photo at a time.',
  'Every new word learned is a step forward.',
  'You are the best teacher your child will ever have.',
  'Small lessons today, big achievements tomorrow.',
];

export default function HomeScreen({ navigation }) {
  const { history, stats, refreshHistory } = useContext(AIContext);
  const { user } = useContext(AuthContext);
  const recent = history.slice(0, 5);

  const heroOp = useRef(new Animated.Value(0)).current;
  const heroY = useRef(new Animated.Value(30)).current;
  const statsOp = useRef(new Animated.Value(0)).current;
  const statsY = useRef(new Animated.Value(40)).current;
  const ctaOp = useRef(new Animated.Value(0)).current;
  const ctaScale = useRef(new Animated.Value(0.9)).current;
  const restOp = useRef(new Animated.Value(0)).current;
  const restY = useRef(new Animated.Value(30)).current;
  const orbPulse = useRef(new Animated.Value(0.3)).current;

  const [qi, setQi] = useState(0);
  const quoteOp = useRef(new Animated.Value(1)).current;

  useFocusEffect(useCallback(() => { refreshHistory(); }, [refreshHistory]));

  useEffect(() => {
    Animated.stagger(200, [
      Animated.parallel([
        Animated.timing(heroOp, { toValue: 1, duration: 600, useNativeDriver: true }),
        Animated.timing(heroY, { toValue: 0, duration: 600, easing: Easing.out(Easing.cubic), useNativeDriver: true }),
      ]),
      Animated.parallel([
        Animated.timing(statsOp, { toValue: 1, duration: 500, useNativeDriver: true }),
        Animated.timing(statsY, { toValue: 0, duration: 500, easing: Easing.out(Easing.cubic), useNativeDriver: true }),
      ]),
      Animated.parallel([
        Animated.spring(ctaScale, { toValue: 1, friction: 5, tension: 60, useNativeDriver: true }),
        Animated.timing(ctaOp, { toValue: 1, duration: 400, useNativeDriver: true }),
      ]),
      Animated.parallel([
        Animated.timing(restOp, { toValue: 1, duration: 500, useNativeDriver: true }),
        Animated.timing(restY, { toValue: 0, duration: 500, useNativeDriver: true }),
      ]),
    ]).start();

    Animated.loop(Animated.sequence([
      Animated.timing(orbPulse, { toValue: 0.7, duration: 3000, useNativeDriver: true }),
      Animated.timing(orbPulse, { toValue: 0.3, duration: 3000, useNativeDriver: true }),
    ])).start();

    const iv = setInterval(() => {
      Animated.timing(quoteOp, { toValue: 0, duration: 250, useNativeDriver: true }).start(() => {
        setQi(p => (p + 1) % QUOTES.length);
        Animated.timing(quoteOp, { toValue: 1, duration: 250, useNativeDriver: true }).start();
      });
    }, 5000);
    return () => clearInterval(iv);
  }, []);

  return (
    <View style={s.root}>
      <StatusBar barStyle="light-content" backgroundColor={COLORS.bgDeep} />
      <ScrollView showsVerticalScrollIndicator={false}>
        {/* Hero */}
        <Animated.View style={{ opacity: heroOp, transform: [{ translateY: heroY }] }}>
          <LinearGradient colors={GRADIENTS.hero} style={s.hero}>
            <Animated.View style={[s.orb, s.orb1, { opacity: orbPulse }]} />
            <Animated.View style={[s.orb, s.orb2, { opacity: orbPulse }]} />
            <View style={s.heroTop}>
              <View>
                <Text style={s.greeting}>Hello, {user?.childName || 'Learner'}</Text>
                <Text style={s.heroSub}>Ready to learn something new?</Text>
              </View>
              <TouchableOpacity onPress={() => navigation.navigate('ProfileTab')}>
                <LinearGradient colors={GRADIENTS.accent} style={s.avatarSmall}>
                  <Text style={s.avatarText}>{user?.avatar || '?'}</Text>
                </LinearGradient>
              </TouchableOpacity>
            </View>
          </LinearGradient>
        </Animated.View>

        {/* Stats */}
        <Animated.View style={[s.statsRow, { opacity: statsOp, transform: [{ translateY: statsY }] }]}>
          <View style={s.statCard}>
            <IconBadge name="camera-outline" size={18} gradient={GRADIENTS.accent} bgSize={36} radius={12} />
            <Text style={s.statNum}>{stats.totalScans || 0}</Text>
            <Text style={s.statLabel}>Scanned</Text>
          </View>
          <View style={s.statCard}>
            <IconBadge name="bookmark-outline" size={18} gradient={GRADIENTS.purple} bgSize={36} radius={12} />
            <Text style={s.statNum}>{stats.totalSaved || 0}</Text>
            <Text style={s.statLabel}>Saved</Text>
          </View>
          <View style={s.statCard}>
            <IconBadge name="trending-up-outline" size={18} gradient={GRADIENTS.blue} bgSize={36} radius={12} />
            <Text style={s.statNum}>{stats.totalSaved ? Math.min(stats.totalSaved * 10, 100) : 0}%</Text>
            <Text style={s.statLabel}>Progress</Text>
          </View>
        </Animated.View>

        {/* CTA */}
        <Animated.View style={[s.ctaWrap, { opacity: ctaOp, transform: [{ scale: ctaScale }] }]}>
          <TouchableOpacity activeOpacity={0.85} onPress={() => navigation.navigate('CameraTab')}>
            <LinearGradient colors={GRADIENTS.accent} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={s.ctaBtn}>
              <Ionicons name="scan-outline" size={20} color={COLORS.white} />
              <Text style={s.ctaText}>Scan Text Now</Text>
              <View style={s.ctaArrow}>
                <Ionicons name="arrow-forward" size={16} color={COLORS.white} />
              </View>
            </LinearGradient>
          </TouchableOpacity>
        </Animated.View>

        <Animated.View style={{ opacity: restOp, transform: [{ translateY: restY }] }}>
          {/* Motivation */}
          <View style={s.section}>
            <Text style={s.secTitle}>Daily Motivation</Text>
            <View style={s.quoteCard}>
              <Ionicons name="chatbubble-ellipses-outline" size={18} color={COLORS.accent} />
              <Animated.Text style={[s.quoteText, { opacity: quoteOp }]}>{QUOTES[qi]}</Animated.Text>
              <View style={s.dots}>
                {QUOTES.map((_, i) => <View key={i} style={[s.dot, i === qi && s.dotActive]} />)}
              </View>
            </View>
          </View>

          {/* How it works */}
          <View style={s.section}>
            <Text style={s.secTitle}>How It Works</Text>
            <View style={s.stepsRow}>
              {[
                { icon: 'camera-outline', label: 'Capture', color: COLORS.accent },
                { icon: 'sparkles-outline', label: 'Analyze', color: COLORS.accentPink },
                { icon: 'book-outline', label: 'Learn', color: COLORS.cyan },
              ].map((st, i) => (
                <React.Fragment key={i}>
                  {i > 0 && (
                    <View style={s.stepConnector}>
                      <View style={s.stepConnectorLine} />
                    </View>
                  )}
                  <View style={s.step}>
                    <CircleIcon name={st.icon} color={st.color} size={22} bgSize={50} />
                    <Text style={s.stepLabel}>{st.label}</Text>
                  </View>
                </React.Fragment>
              ))}
            </View>
          </View>

          {/* Why KidTutor */}
          <View style={s.section}>
            <Text style={s.secTitle}>Why KidTutor?</Text>
            <View style={s.whyCard}>
              <LinearGradient colors={GRADIENTS.cardAccent} style={s.whyGradientBg}>
                <Text style={s.whyHeadline}>
                  Makes your books{'\n'}
                  <Text style={{ color: COLORS.accent }}>very simple</Text> to understand
                </Text>
                <Text style={s.whySubtext}>
                  Your child's learning speed increases when complex topics become easy and fun.
                </Text>
              </LinearGradient>

              {/* Benefit cards */}
              <View style={s.benefitRow}>
                <View style={[s.benefitCard, { borderColor: COLORS.accent + '30' }]}>
                  <LinearGradient colors={GRADIENTS.accent} style={s.benefitIconWrap}>
                    <Ionicons name="book-outline" size={20} color={COLORS.white} />
                  </LinearGradient>
                  <Text style={s.benefitTitle}>Simple Summaries</Text>
                  <Text style={s.benefitDesc}>Hard textbook chapters become 2-3 easy sentences any child can understand</Text>
                </View>
                <View style={[s.benefitCard, { borderColor: COLORS.accentPink + '30' }]}>
                  <LinearGradient colors={['#EC4899', '#F472B6']} style={s.benefitIconWrap}>
                    <Ionicons name="rocket-outline" size={20} color={COLORS.white} />
                  </LinearGradient>
                  <Text style={s.benefitTitle}>Faster Learning</Text>
                  <Text style={s.benefitDesc}>Visual explanations help kids learn 3x faster than reading alone</Text>
                </View>
              </View>

              <View style={s.benefitRow}>
                <View style={[s.benefitCard, { borderColor: COLORS.cyan + '30' }]}>
                  <LinearGradient colors={GRADIENTS.blue} style={s.benefitIconWrap}>
                    <Ionicons name="globe-outline" size={20} color={COLORS.white} />
                  </LinearGradient>
                  <Text style={s.benefitTitle}>Real World Examples</Text>
                  <Text style={s.benefitDesc}>Connect lessons to everyday life  toys, games, school, and nature</Text>
                </View>
                <View style={[s.benefitCard, { borderColor: COLORS.success + '30' }]}>
                  <LinearGradient colors={['#34D399', '#22D3EE']} style={s.benefitIconWrap}>
                    <Ionicons name="key-outline" size={20} color={COLORS.white} />
                  </LinearGradient>
                  <Text style={s.benefitTitle}>Key Words</Text>
                  <Text style={s.benefitDesc}>Important vocabulary highlighted so kids build a strong word bank</Text>
                </View>
              </View>

              {/* Highlight banner */}
              <LinearGradient colors={GRADIENTS.accent} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={s.whyBanner}>
                <Ionicons name="sparkles" size={18} color={COLORS.white} />
                <Text style={s.whyBannerText}>Snap a photo. Learn in seconds.</Text>
                <Ionicons name="arrow-forward" size={16} color={COLORS.white} />
              </LinearGradient>
            </View>
          </View>

          {/* About KidTutor */}
          <View style={s.section}>
            <Text style={s.secTitle}>About KidTutor</Text>
            <View style={s.aboutCard}>
              <View style={s.aboutHeader}>
                <IconBadge name="sparkles" size={20} gradient={GRADIENTS.accent} bgSize={40} radius={14} />
                <View style={{ flex: 1, marginLeft: 14 }}>
                  <Text style={s.aboutAppName}>KidTutor</Text>
                  <Text style={s.aboutTagline}>AI-Powered Learning for Every Child</Text>
                </View>
              </View>
              <Text style={s.aboutDesc}>
                KidTutor helps parents who want the best education for their children. Simply take a photo of any text  from textbooks, signs, or homework  and our AI instantly explains it in simple, kid-friendly language.
              </Text>
              <View style={s.aboutFeatures}>
                {[
                  { icon: 'flash-outline', label: 'Instant AI Analysis', desc: 'Powered by advanced AI', color: COLORS.accent },
                  { icon: 'shield-checkmark-outline', label: 'Safe & Private', desc: 'Data stays on your device', color: COLORS.success },
                  { icon: 'happy-outline', label: 'Ages 612', desc: 'Tailored for young learners', color: COLORS.accentPink },
                  { icon: 'wallet-outline', label: '100% Free', desc: 'No hidden charges ever', color: COLORS.cyan },
                ].map((f, i) => (
                  <View key={i} style={s.aboutFeatureRow}>
                    <GlowIcon name={f.icon} color={f.color} size={18} bgSize={36} />
                    <View style={s.aboutFeatureText}>
                      <Text style={s.aboutFeatureLabel}>{f.label}</Text>
                      <Text style={s.aboutFeatureDesc}>{f.desc}</Text>
                    </View>
                  </View>
                ))}
              </View>
            </View>
          </View>

          {/* Recent */}
          {recent.length > 0 && (
            <View style={s.section}>
              <View style={s.secHeader}>
                <Text style={s.secTitle}>Recent Lessons</Text>
                <TouchableOpacity onPress={() => navigation.navigate('HistoryTab')}>
                  <Text style={s.seeAll}>See all</Text>
                </TouchableOpacity>
              </View>
              <FlatList
                data={recent} horizontal showsHorizontalScrollIndicator={false}
                keyExtractor={item => item.id}
                renderItem={({ item }) => (
                  <HistoryCard item={item} compact onPress={() => navigation.navigate('HomeDetail', { item })} />
                )}
              />
            </View>
          )}

          {/* Footer */}
          <View style={[s.section, { marginBottom: 20 }]}>
            <LinearGradient colors={GRADIENTS.accentSoft} style={s.footerCard}>
              <Ionicons name="heart-outline" size={22} color={COLORS.accentLight} />
              <Text style={s.footerText}>Every child deserves to learn.{'\n'}<Text style={{ color: COLORS.accentLight, fontWeight: '700' }}>You are doing great!</Text></Text>
            </LinearGradient>
          </View>
        </Animated.View>

        <View style={{ height: 100 }} />
      </ScrollView>
    </View>
  );
}

const s = {
  root: { flex: 1, backgroundColor: COLORS.bgDeep },
  hero: { paddingTop: 60, paddingBottom: 32, paddingHorizontal: SPACING.lg, borderBottomLeftRadius: 24, borderBottomRightRadius: 24, overflow: 'hidden' },
  orb: { position: 'absolute', borderRadius: 999 },
  orb1: { width: 200, height: 200, backgroundColor: COLORS.accentGlow, top: -60, right: -60 },
  orb2: { width: 160, height: 160, backgroundColor: COLORS.cyanGlow, bottom: -40, left: -40 },
  heroTop: { flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', zIndex: 2 },
  greeting: { ...FONTS.h1, color: COLORS.textPrimary },
  heroSub: { ...FONTS.body, color: COLORS.textSecondary, marginTop: 2 },
  avatarSmall: { width: 44, height: 44, borderRadius: 15, justifyContent: 'center', alignItems: 'center' },
  avatarText: { fontSize: 18, fontWeight: '800', color: COLORS.white },
  statsRow: { flexDirection: 'row', paddingHorizontal: SPACING.lg, marginTop: -16, gap: SPACING.sm, zIndex: 5 },
  statCard: { flex: 1, backgroundColor: COLORS.bgCard, borderRadius: RADIUS.xl, padding: 18, alignItems: 'center', borderWidth: 1, borderColor: COLORS.border, ...SHADOWS.card },
  statNum: { fontSize: 24, fontWeight: '800', color: COLORS.textPrimary, marginTop: 6 },
  statLabel: { ...FONTS.caption, color: COLORS.textMuted, marginTop: 2 },
  ctaWrap: { paddingHorizontal: SPACING.lg, marginTop: SPACING.lg },
  ctaBtn: { flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: 10, paddingVertical: 16, borderRadius: RADIUS.xxl, ...SHADOWS.accentGlow },
  ctaText: { ...FONTS.buttonLarge, color: COLORS.white },
  ctaArrow: { width: 28, height: 28, borderRadius: 14, backgroundColor: 'rgba(255,255,255,0.2)', justifyContent: 'center', alignItems: 'center' },
  section: { marginTop: SPACING.lg, paddingHorizontal: SPACING.lg },
  secHeader: { flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center', marginBottom: 12 },
  secTitle: { ...FONTS.h3, color: COLORS.textPrimary, marginBottom: 12 },
  seeAll: { ...FONTS.bodySmall, color: COLORS.accent, fontWeight: '600' },
  quoteCard: { backgroundColor: COLORS.bgCard, borderRadius: RADIUS.xl, padding: 18, alignItems: 'center', borderWidth: 1, borderColor: COLORS.border, gap: 10 },
  quoteText: { ...FONTS.body, color: COLORS.textSecondary, textAlign: 'center', fontStyle: 'italic', lineHeight: 22 },
  dots: { flexDirection: 'row', gap: 5 },
  dot: { width: 6, height: 6, borderRadius: 3, backgroundColor: COLORS.textMuted },
  dotActive: { width: 18, backgroundColor: COLORS.accent },
  stepsRow: { flexDirection: 'row', alignItems: 'center', justifyContent: 'center' },
  step: { alignItems: 'center', flex: 1 },
  stepLabel: { ...FONTS.caption, color: COLORS.textSecondary, marginTop: 8, fontWeight: '600' },
  stepConnector: { width: 30, alignItems: 'center', paddingBottom: SPACING.lg },
  stepConnectorLine: { width: 24, height: 1.5, backgroundColor: COLORS.border },
  footerCard: { borderRadius: RADIUS.xl, padding: 18, alignItems: 'center', borderWidth: 1, borderColor: COLORS.borderAccent, gap: 8 },
  footerText: { ...FONTS.body, color: COLORS.textSecondary, textAlign: 'center', lineHeight: 22 },
  // Why KidTutor section
  whyCard: { backgroundColor: COLORS.bgCard, borderRadius: RADIUS.xl, overflow: 'hidden', borderWidth: 1, borderColor: COLORS.border, ...SHADOWS.card },
  whyGradientBg: { padding: 20, paddingBottom: 16 },
  whyHeadline: { ...FONTS.h2, color: COLORS.textPrimary, lineHeight: 30, marginBottom: 8 },
  whySubtext: { ...FONTS.body, color: COLORS.textSecondary, lineHeight: 22 },
  benefitRow: { flexDirection: 'row', gap: SPACING.sm, paddingHorizontal: 10, marginTop: SPACING.sm },
  benefitCard: { flex: 1, backgroundColor: COLORS.bgSecondary, borderRadius: RADIUS.lg, padding: 14, borderWidth: 1, alignItems: 'center' },
  benefitIconWrap: { width: 40, height: 40, borderRadius: 13, justifyContent: 'center', alignItems: 'center', marginBottom: 10 },
  benefitTitle: { ...FONTS.bodySmall, color: COLORS.textPrimary, fontWeight: '700', textAlign: 'center', marginBottom: 4 },
  benefitDesc: { ...FONTS.caption, color: COLORS.textMuted, textAlign: 'center', lineHeight: 16 },
  whyBanner: { flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: 8, paddingVertical: 14, margin: 10, marginTop: 12, borderRadius: RADIUS.lg },
  whyBannerText: { ...FONTS.button, color: COLORS.white },
  // About section
  aboutCard: { backgroundColor: COLORS.bgCard, borderRadius: RADIUS.xl, padding: 18, borderWidth: 1, borderColor: COLORS.border, ...SHADOWS.card },
  aboutHeader: { flexDirection: 'row', alignItems: 'center', marginBottom: 12 },
  aboutAppName: { ...FONTS.h3, color: COLORS.textPrimary },
  aboutTagline: { ...FONTS.caption, color: COLORS.textMuted, marginTop: 2 },
  aboutDesc: { ...FONTS.body, color: COLORS.textSecondary, lineHeight: 22, marginBottom: 14 },
  aboutFeatures: { gap: 10 },
  aboutFeatureRow: { flexDirection: 'row', alignItems: 'center' },
  aboutFeatureText: { marginLeft: 12, flex: 1 },
  aboutFeatureLabel: { ...FONTS.bodySmall, color: COLORS.textPrimary, fontWeight: '600' },
  aboutFeatureDesc: { ...FONTS.caption, color: COLORS.textMuted, marginTop: 1 },
};
