import React, { useState, useContext, useEffect, useRef } from 'react';
import {
  View, Text, TextInput, TouchableOpacity, StyleSheet,
  KeyboardAvoidingView, Platform, ScrollView, StatusBar, Animated, Easing,
} from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { Ionicons } from '@expo/vector-icons';
import { AuthContext } from '../contexts/AuthContext';
import { AppLogo, GlowIcon } from '../components/AppIcon';
import { COLORS, GRADIENTS, SPACING, RADIUS, FONTS, SHADOWS } from '../constants/theme';

export default function RegisterScreen({ navigation }) {
  const { register } = useContext(AuthContext);
  const [step, setStep] = useState(1);
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [childName, setChildName] = useState('');
  const [childAge, setChildAge] = useState('');
  const [showPw, setShowPw] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [focusedField, setFocusedField] = useState(null);

  const fadeIn = useRef(new Animated.Value(0)).current;
  const slideUp = useRef(new Animated.Value(40)).current;

  useEffect(() => {
    Animated.parallel([
      Animated.timing(fadeIn, { toValue: 1, duration: 700, useNativeDriver: true }),
      Animated.timing(slideUp, { toValue: 0, duration: 700, easing: Easing.out(Easing.cubic), useNativeDriver: true }),
    ]).start();
  }, []);

  const goNext = () => {
    setError('');
    if (!name.trim()) return setError('Enter your name');
    if (!email.trim()) return setError('Enter your email');
    if (password.length < 4) return setError('Password must be 4+ characters');
    setStep(2);
  };

  const handleRegister = async () => {
    setError('');
    if (!childName.trim()) return setError("Enter your child's name");
    if (!childAge.trim()) return setError("Enter your child's age");
    setLoading(true);
    const res = await register({ name: name.trim(), email: email.trim(), password, childName: childName.trim(), childAge: childAge.trim() });
    setLoading(false);
    if (!res.success) setError(res.error);
  };

  const isFocused = (field) => focusedField === field;

  return (
    <View style={s.root}>
      <StatusBar barStyle="light-content" />
      <KeyboardAvoidingView behavior={Platform.OS === 'ios' ? 'padding' : 'height'} style={{ flex: 1 }}>
        <ScrollView contentContainerStyle={s.scroll} showsVerticalScrollIndicator={false} keyboardShouldPersistTaps="handled">
          <TouchableOpacity style={s.back} onPress={() => step === 2 ? setStep(1) : navigation.goBack()}>
            <Ionicons name="chevron-back" size={22} color={COLORS.textSecondary} />
          </TouchableOpacity>

          <Animated.View style={{ opacity: fadeIn, transform: [{ translateY: slideUp }] }}>
            {/* Header */}
            <View style={s.header}>
              <AppLogo size={60} />
              <Text style={s.title}>Create Account</Text>
              <Text style={s.sub}>Start your child's learning journey</Text>
            </View>

            {/* Step indicator */}
            <View style={s.steps}>
              <View style={[s.stepDot, step >= 1 && s.stepActive]}>
                <Text style={[s.stepNum, step >= 1 && s.stepNumActive]}>1</Text>
              </View>
              <View style={[s.stepLine, step >= 2 && s.stepLineActive]} />
              <View style={[s.stepDot, step >= 2 && s.stepActive]}>
                <Text style={[s.stepNum, step >= 2 && s.stepNumActive]}>2</Text>
              </View>
            </View>
            <View style={s.stepLabels}>
              <Text style={[s.stepLabel, step === 1 && s.stepLabelActive]}>Your details</Text>
              <Text style={[s.stepLabel, step === 2 && s.stepLabelActive]}>Child info</Text>
            </View>

            {/* Error */}
            {error ? (
              <View style={s.errBox}>
                <Ionicons name="alert-circle" size={16} color={COLORS.danger} />
                <Text style={s.errText}>{error}</Text>
              </View>
            ) : null}

            {step === 1 ? (
              <>
                <Text style={s.label}>Full name</Text>
                <View style={[s.inputBox, isFocused('name') && s.inputFocused]}>
                  <Ionicons name="person-outline" size={18} color={isFocused('name') ? COLORS.accent : COLORS.textMuted} />
                  <TextInput
                    style={s.input} value={name} onChangeText={setName}
                    placeholder="Your full name" placeholderTextColor={COLORS.textMuted}
                    onFocus={() => setFocusedField('name')} onBlur={() => setFocusedField(null)}
                  />
                </View>

                <Text style={s.label}>Email</Text>
                <View style={[s.inputBox, isFocused('email') && s.inputFocused]}>
                  <Ionicons name="mail-outline" size={18} color={isFocused('email') ? COLORS.accent : COLORS.textMuted} />
                  <TextInput
                    style={s.input} value={email} onChangeText={setEmail}
                    placeholder="you@example.com" placeholderTextColor={COLORS.textMuted}
                    keyboardType="email-address" autoCapitalize="none"
                    onFocus={() => setFocusedField('email')} onBlur={() => setFocusedField(null)}
                  />
                </View>

                <Text style={s.label}>Password</Text>
                <View style={[s.inputBox, isFocused('password') && s.inputFocused]}>
                  <Ionicons name="lock-closed-outline" size={18} color={isFocused('password') ? COLORS.accent : COLORS.textMuted} />
                  <TextInput
                    style={s.input} value={password} onChangeText={setPassword}
                    placeholder="Min 4 characters" placeholderTextColor={COLORS.textMuted}
                    secureTextEntry={!showPw}
                    onFocus={() => setFocusedField('password')} onBlur={() => setFocusedField(null)}
                  />
                  <TouchableOpacity onPress={() => setShowPw(!showPw)} hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}>
                    <Ionicons name={showPw ? 'eye-off-outline' : 'eye-outline'} size={18} color={COLORS.textMuted} />
                  </TouchableOpacity>
                </View>

                <TouchableOpacity activeOpacity={0.85} onPress={goNext} style={{ marginTop: 12 }}>
                  <LinearGradient colors={GRADIENTS.accent} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={s.btn}>
                    <Text style={s.btnText}>Continue</Text>
                    <Ionicons name="arrow-forward" size={18} color={COLORS.white} />
                  </LinearGradient>
                </TouchableOpacity>
              </>
            ) : (
              <>
                <Text style={s.label}>Child's name</Text>
                <View style={[s.inputBox, isFocused('childName') && s.inputFocused]}>
                  <Ionicons name="happy-outline" size={18} color={isFocused('childName') ? COLORS.accent : COLORS.textMuted} />
                  <TextInput
                    style={s.input} value={childName} onChangeText={setChildName}
                    placeholder="Your child's first name" placeholderTextColor={COLORS.textMuted}
                    onFocus={() => setFocusedField('childName')} onBlur={() => setFocusedField(null)}
                  />
                </View>

                <Text style={s.label}>Child's age</Text>
                <View style={[s.inputBox, isFocused('childAge') && s.inputFocused]}>
                  <Ionicons name="calendar-outline" size={18} color={isFocused('childAge') ? COLORS.accent : COLORS.textMuted} />
                  <TextInput
                    style={s.input} value={childAge} onChangeText={setChildAge}
                    placeholder="e.g. 8" placeholderTextColor={COLORS.textMuted}
                    keyboardType="number-pad"
                    onFocus={() => setFocusedField('childAge')} onBlur={() => setFocusedField(null)}
                  />
                </View>

                <View style={s.infoCard}>
                  <GlowIcon name="information-circle-outline" size={18} color={COLORS.accent} bgSize={34} />
                  <Text style={s.infoText}>We personalize explanations based on your child's age for better understanding.</Text>
                </View>

                <TouchableOpacity activeOpacity={0.85} onPress={handleRegister} disabled={loading} style={{ marginTop: 4 }}>
                  <LinearGradient colors={GRADIENTS.accent} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={s.btn}>
                    <Text style={s.btnText}>{loading ? 'Creating...' : 'Create Account'}</Text>
                    {!loading && <Ionicons name="checkmark" size={18} color={COLORS.white} />}
                  </LinearGradient>
                </TouchableOpacity>
              </>
            )}

            <TouchableOpacity style={s.loginRow} onPress={() => navigation.navigate('Login')}>
              <Text style={s.loginText}>Already have an account? <Text style={s.loginLink}>Sign In</Text></Text>
            </TouchableOpacity>
          </Animated.View>
        </ScrollView>
      </KeyboardAvoidingView>
    </View>
  );
}

const s = StyleSheet.create({
  root: { flex: 1, backgroundColor: COLORS.bgDeep },
  scroll: { flexGrow: 1, padding: 24, paddingTop: 60 },
  back: {
    width: 40, height: 40, borderRadius: 14, backgroundColor: COLORS.bgCard,
    justifyContent: 'center', alignItems: 'center', borderWidth: 1, borderColor: COLORS.border, marginBottom: 16,
  },
  header: { alignItems: 'center', marginBottom: 24 },
  title: { ...FONTS.h1, color: COLORS.textPrimary, marginTop: 14 },
  sub: { ...FONTS.body, color: COLORS.textMuted, marginTop: 4 },
  steps: { flexDirection: 'row', alignItems: 'center', justifyContent: 'center', marginBottom: 6 },
  stepDot: {
    width: 34, height: 34, borderRadius: 17, justifyContent: 'center', alignItems: 'center',
    backgroundColor: COLORS.bgCard, borderWidth: 2, borderColor: COLORS.border,
  },
  stepActive: { backgroundColor: COLORS.accent, borderColor: COLORS.accent },
  stepNum: { ...FONTS.button, fontSize: 13, color: COLORS.textMuted },
  stepNumActive: { color: COLORS.white },
  stepLine: { width: 50, height: 2, backgroundColor: COLORS.border, marginHorizontal: 6 },
  stepLineActive: { backgroundColor: COLORS.accent },
  stepLabels: { flexDirection: 'row', justifyContent: 'center', gap: 40, marginBottom: 20 },
  stepLabel: { ...FONTS.caption, color: COLORS.textMuted },
  stepLabelActive: { color: COLORS.accent, fontWeight: '700' },
  errBox: {
    flexDirection: 'row', alignItems: 'center', gap: 8, padding: 14,
    backgroundColor: COLORS.danger + '12', borderRadius: RADIUS.lg, marginBottom: 12,
    borderWidth: 1, borderColor: COLORS.danger + '25',
  },
  errText: { ...FONTS.bodySmall, color: COLORS.danger, flex: 1 },
  label: { ...FONTS.label, color: COLORS.textMuted, marginBottom: 8, marginTop: 14, marginLeft: 4 },
  inputBox: {
    flexDirection: 'row', alignItems: 'center', gap: 12, height: 54,
    backgroundColor: COLORS.bgInput, borderRadius: RADIUS.xl, paddingHorizontal: 16,
    borderWidth: 1.5, borderColor: COLORS.border,
  },
  inputFocused: { borderColor: COLORS.accent + '60', backgroundColor: COLORS.bgCard },
  input: { flex: 1, ...FONTS.body, color: COLORS.textPrimary, height: '100%' },
  infoCard: {
    flexDirection: 'row', alignItems: 'center', gap: 12, padding: 14,
    backgroundColor: COLORS.accentGlow, borderRadius: RADIUS.lg, marginTop: 8, marginBottom: 12,
    borderWidth: 1, borderColor: COLORS.borderAccent,
  },
  infoText: { ...FONTS.bodySmall, color: COLORS.textSecondary, flex: 1, lineHeight: 19 },
  btn: {
    flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: 10,
    paddingVertical: 16, borderRadius: RADIUS.xxl, ...SHADOWS.accentGlow,
  },
  btnText: { ...FONTS.buttonLarge, color: COLORS.white },
  loginRow: { alignItems: 'center', marginTop: 24, marginBottom: 40, paddingVertical: 8 },
  loginText: { ...FONTS.body, color: COLORS.textMuted },
  loginLink: { color: COLORS.accentLight, fontWeight: '700' },
});
