import React, { useState, useContext, useEffect, useRef } from 'react';
import {
  View, Text, TextInput, TouchableOpacity, StyleSheet,
  KeyboardAvoidingView, Platform, ScrollView, StatusBar, Animated, Easing,
} from 'react-native';
import { LinearGradient } from 'expo-linear-gradient';
import { Ionicons } from '@expo/vector-icons';
import { AuthContext } from '../contexts/AuthContext';
import { AppLogo } from '../components/AppIcon';
import { COLORS, GRADIENTS, SPACING, RADIUS, FONTS, SHADOWS } from '../constants/theme';

export default function LoginScreen({ navigation }) {
  const { login } = useContext(AuthContext);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [showPw, setShowPw] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const [focusedField, setFocusedField] = useState(null);

  const fadeIn = useRef(new Animated.Value(0)).current;
  const slideUp = useRef(new Animated.Value(40)).current;

  useEffect(() => {
    Animated.parallel([
      Animated.timing(fadeIn, { toValue: 1, duration: 700, useNativeDriver: true }),
      Animated.timing(slideUp, { toValue: 0, duration: 700, easing: Easing.out(Easing.cubic), useNativeDriver: true }),
    ]).start();
  }, []);

  const isFocused = (field) => focusedField === field;

  const handleLogin = async () => {
    setError('');
    if (!email.trim() || !password.trim()) { setError('Please fill in all fields'); return; }
    setLoading(true);
    const res = await login(email.trim(), password);
    setLoading(false);
    if (!res.success) setError(res.error);
  };

  return (
    <View style={s.root}>
      <StatusBar barStyle="light-content" />
      <KeyboardAvoidingView behavior={Platform.OS === 'ios' ? 'padding' : 'height'} style={{ flex: 1 }}>
        <ScrollView contentContainerStyle={s.scroll} showsVerticalScrollIndicator={false} keyboardShouldPersistTaps="handled">
          {/* Back */}
          <TouchableOpacity style={s.back} onPress={() => navigation.goBack()}>
            <Ionicons name="chevron-back" size={22} color={COLORS.textSecondary} />
          </TouchableOpacity>

          <Animated.View style={{ opacity: fadeIn, transform: [{ translateY: slideUp }] }}>
            {/* Header */}
            <View style={s.header}>
              <AppLogo size={64} />
              <Text style={s.title}>Welcome back</Text>
              <Text style={s.sub}>Sign in to continue your journey</Text>
            </View>

            {/* Error */}
            {error ? (
              <View style={s.errBox}>
                <Ionicons name="alert-circle" size={16} color={COLORS.danger} />
                <Text style={s.errText}>{error}</Text>
              </View>
            ) : null}

            {/* Email */}
            <Text style={s.label}>Email</Text>
            <View style={[s.inputBox, isFocused('email') && s.inputFocused]}>
              <Ionicons name="mail-outline" size={18} color={isFocused('email') ? COLORS.accent : COLORS.textMuted} />
              <TextInput
                style={s.input} value={email} onChangeText={setEmail}
                placeholder="you@example.com" placeholderTextColor={COLORS.textMuted}
                keyboardType="email-address" autoCapitalize="none"
                onFocus={() => setFocusedField('email')} onBlur={() => setFocusedField(null)}
              />
            </View>

            {/* Password */}
            <Text style={s.label}>Password</Text>
            <View style={[s.inputBox, isFocused('password') && s.inputFocused]}>
              <Ionicons name="lock-closed-outline" size={18} color={isFocused('password') ? COLORS.accent : COLORS.textMuted} />
              <TextInput
                style={s.input} value={password} onChangeText={setPassword}
                placeholder="Enter password" placeholderTextColor={COLORS.textMuted}
                secureTextEntry={!showPw}
                onFocus={() => setFocusedField('password')} onBlur={() => setFocusedField(null)}
              />
              <TouchableOpacity onPress={() => setShowPw(!showPw)} hitSlop={{ top: 10, bottom: 10, left: 10, right: 10 }}>
                <Ionicons name={showPw ? 'eye-off-outline' : 'eye-outline'} size={18} color={COLORS.textMuted} />
              </TouchableOpacity>
            </View>

            {/* Sign In */}
            <TouchableOpacity activeOpacity={0.85} onPress={handleLogin} disabled={loading} style={{ marginTop: 8 }}>
              <LinearGradient colors={GRADIENTS.accent} start={{ x: 0, y: 0 }} end={{ x: 1, y: 0 }} style={s.btn}>
                {loading ? (
                  <Text style={s.btnText}>Signing in...</Text>
                ) : (
                  <>
                    <Text style={s.btnText}>Sign In</Text>
                    <Ionicons name="arrow-forward" size={18} color={COLORS.white} />
                  </>
                )}
              </LinearGradient>
            </TouchableOpacity>

            {/* Divider */}
            <View style={s.divider}>
              <View style={s.divLine} />
              <Text style={s.divText}>or</Text>
              <View style={s.divLine} />
            </View>

            {/* Register */}
            <TouchableOpacity style={s.outlineBtn} onPress={() => navigation.navigate('Register')}>
              <Ionicons name="person-add-outline" size={18} color={COLORS.accent} />
              <Text style={s.outlineBtnText}>Create a new account</Text>
            </TouchableOpacity>
          </Animated.View>
        </ScrollView>
      </KeyboardAvoidingView>
    </View>
  );
}

const s = StyleSheet.create({
  root: { flex: 1, backgroundColor: COLORS.bgDeep },
  scroll: { flexGrow: 1, padding: 24, paddingTop: 60 },
  back: {
    width: 40, height: 40, borderRadius: 14, backgroundColor: COLORS.bgCard,
    justifyContent: 'center', alignItems: 'center', borderWidth: 1, borderColor: COLORS.border, marginBottom: 24,
  },
  header: { alignItems: 'center', marginBottom: 28 },
  title: { ...FONTS.h1, color: COLORS.textPrimary, marginTop: 16 },
  sub: { ...FONTS.body, color: COLORS.textMuted, marginTop: 4 },
  errBox: {
    flexDirection: 'row', alignItems: 'center', gap: 8, padding: 14,
    backgroundColor: COLORS.danger + '12', borderRadius: RADIUS.lg, marginBottom: 16,
    borderWidth: 1, borderColor: COLORS.danger + '25',
  },
  errText: { ...FONTS.bodySmall, color: COLORS.danger, flex: 1 },
  label: { ...FONTS.label, color: COLORS.textMuted, marginBottom: 8, marginTop: 16, marginLeft: 4 },
  inputBox: {
    flexDirection: 'row', alignItems: 'center', gap: 12, height: 54,
    backgroundColor: COLORS.bgInput, borderRadius: RADIUS.xl, paddingHorizontal: 16,
    borderWidth: 1.5, borderColor: COLORS.border,
  },
  inputFocused: { borderColor: COLORS.accent + '60', backgroundColor: COLORS.bgCard },
  input: { flex: 1, ...FONTS.body, color: COLORS.textPrimary, height: '100%' },
  btn: {
    flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: 10,
    paddingVertical: 16, borderRadius: RADIUS.xxl, ...SHADOWS.accentGlow,
  },
  btnText: { ...FONTS.buttonLarge, color: COLORS.white },
  divider: { flexDirection: 'row', alignItems: 'center', marginVertical: 24 },
  divLine: { flex: 1, height: 1, backgroundColor: COLORS.border },
  divText: { ...FONTS.caption, color: COLORS.textMuted, marginHorizontal: 16 },
  outlineBtn: {
    flexDirection: 'row', alignItems: 'center', justifyContent: 'center', gap: 10,
    paddingVertical: 15, borderRadius: RADIUS.xxl, borderWidth: 1.5, borderColor: COLORS.borderAccent,
  },
  outlineBtnText: { ...FONTS.button, color: COLORS.accent },
});
